import React, {useEffect, useState} from 'react';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import {inject,observer} from 'mobx-react'
import LoginPage from './pages/auth/login';
import NotFound from './pages/auth/noFound';
import App from './App';
import Dashboard from './pages/dashboard/index'


let Page = inject("appState")(observer((props=>{

    useEffect(()=>{

    })
    return (
        <Router>
            <Switch>
                <Route exact path="/" render={() => <Redirect to="/app/dashboard/index"  component={(props)=><Dashboard {...props}/>} push />} />
                <Route path="/app" component={(props)=><App {...props}/>} />
                <Route path="/404" component={(props)=><NotFound {...props}/>} />
                <Route path="/login" component={(props)=><LoginPage {...props}/>} />
                <Route component={(props)=><NotFound {...props}/>} />
            </Switch>
        </Router>
    );
})))
export default Page
